.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #ED904C;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.main-image {
  width: 100%;
  height: 34rem;  
  background-image: url(//cdn2.editmysite.com/images/editor/theme-background/stock/Cooking.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

